      </el-table>
      <div
        v-show="isIndex == 1"
        style="width: 100%; height: 6rem"
        ref="echart"
      ></div>
    </el-dialog>
  </div>
</template>
<script>
import { analyseNBQDetail } from "@/network/inverter";
export default {
  data() {
    return {
      date1: "",
      date2: "",
      isIndex: 0,
      tabArry: ["数据", "折线图"],
      dialogVisible: false,
      tableData: [],
    };
  },
  watch: {},
  mounted() {},
  methods: {
    closeEvent(){
      this.tableData = []
    },
    showDialogFun(id) {
      this.pvEqmId = id;
      this.date2 = this.date1 =
        this.$getDate().year +
        "-" +
        this.$getDate().month +
        "-" +
        this.$getDate().day;
      this.dialogVisible = true;
      this.analyseNBQDetailFun();
    },
    analyseNBQDetailFun() {
      analyseNBQDetail(this.date1, this.pvEqmId, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;

            if (this.isIndex == 1) {
              setTimeout(() => {
                this.setEchart();
              }, 200);
            }
          }
        })
        .catch((err) => {});
    },
    changeTab(index) {
      this.isIndex = index;
      if (index == 1) {
        setTimeout(() => {
          this.setEchart();
        }, 200);
      }
    },
    setEchart() {
      let series = [];
      let keys = Object.keys(this.tableData[0]);
      keys.forEach((key) => {
        series.push({
          name: key,
          type: "line",
          z: 2,
          lineStyle: {
            color: "#22AFED",
          },
          areaStyle: {
            color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "rgba(0, 255, 149, 0.55)",
              },
              {
                offset: 1,
                color: "rgba(0, 157, 255, 0.55)",
              },
            ]),
          },
          smooth: true,
          data: [],
        });
      });

      let xAxisArry = [];
      this.tableData.forEach((item) => {
        xAxisArry.push(item["时间"]);
        Object.keys(item).forEach((line) => {
          series.forEach((info) => {
            if (info.name == line) {
              info.data.push(item[line]);
            }
          });
        });
      });
      let chart = this.$refs.echart;
      let myChart = this.$echarts.init(chart);
      myChart.clear();
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        dataZoom: [
          {
            type: "inside",
            start: 0,
            end: 10,
          },
          {
            start: 0,
            end: 10,
          },
        ],
        legend: {
          top: "10%",
          textStyle: {
            color: "#fff",
          },
          type: "scroll",
          orient: "vertical",
          left: "0%",
          pageTextStyle: {
            color: "#ffffff", //翻页数字颜色
          },
          pageIconColor: "#2C86FF", //翻页箭头颜色
          pageIconInactiveColor: "rgba(44,132,251,0.40)", //翻页（即翻页到头时箭头的颜色）
          pageIconSize: 10,
        },
        grid: {
          left: "15%",
          right: "1%",
          bottom: "2%",
          top: "10%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: xAxisArry,
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            axisTick: {
              show: false,
            },
          },
        ],
        yAxis: [
          {
            name: "单位（kw）",
            nameLocation: "end",
            nameTextStyle: {
              color: "#fff",
              padding: [0, 10, 0, 0],
            },
            nameGap: 15,
            type: "value",
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255, 255, 255, 0.5)"],
                type: "dashed",
              },
            },
          },
        ],
        series: series,
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
