<template>
  <div class="inverter">
    <div class="search">
      <div>
        <span>企业</span>
        <el-select v-model="value" placeholder="" @change="changeCompany">
          <el-option
            v-for="item in options"
            :key="item.companyId"
            :label="item.companyName"
            :value="item.companyId"
          >
          </el-option>
        </el-select>
      </div>
      <div>
        <span>光伏电站</span>
        <el-select v-model="DZvalue" placeholder="" @change="changeStation">
          <el-option
            v-for="item in DZOptions"
            :key="item.pVStationId"
            :label="item.pVStationName"
            :value="item.pVStationId"
          >
          </el-option>
        </el-select>
      </div>
      <button class="searchBth" @click="analyseFun()">查询</button>
    </div>
    <div class="center" v-show="showData">
      <div v-for="(item, index) in inverterData" :key="index">
        <div class="icon">
          <img
            style="width: 0.55rem"
            :src="require('@/assets/inverter/icon' + index + '.png')"
            alt=""
          />
        </div>
        <p style="flex-grow: 1; text-align: left; padding-left: 0.22rem">
          {{ item }}
        </p>
        <p class="data">{{ inverterValue[index] }}</p>
      </div>
    </div>
    <div class="bottom" v-show="showData">
      <div style="flex-grow: 1; overflow: scroll">
        <div
          v-for="(item, index) in chartArryCurrent"
          :key="index"
          class="inverterBlock"
        >
          <p>{{ item.pvEqmName }}</p>
          <p class="icon" @click="showDeatil(item)">
            <img
              style="width: 0.7rem"
              src="@/assets/inverter/chartIcon.png"
              alt=""
            />
          </p>
          <div class="data">
            <!-- 今日发电量 -->
            <p><span>今日发电量：</span>{{ item.dayElec }}<span>kWh</span></p>
            <!-- 额定功率 -->
            <p><span>额定功率：</span>{{ item.ratedPower }}<span>kW</span></p>
            <!-- 装机容量 -->
            <p>
              <span>装机容量：</span>{{ item.installCapacity }}<span>kWp</span>
            </p>
            <!-- 有效小时 -->
            <p>
              <span>有效小时：</span>{{ item.dayStationActiveHours
              }}<span>h</span>
            </p>
          </div>
          <p class="state" v-if="item.state == '正常'">
            <img
              style="width: 0.26rem"
              src="@/assets/inverter/yes.png"
              alt=""
            />
            {{ item.state }}运行
          </p>
          <p class="state" v-else style="background: #be2121">
            <img style="width: 0.26rem" src="@/assets/inverter/no.png" alt="" />
            {{ item.state }}
          </p>
        </div>
      </div>
      <el-pagination
        background
        layout="prev, pager, next"
        :page-size="8"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :total="total"
      >
      </el-pagination>
    </div>
    <inverterDialog ref="dialog"></inverterDialog>
  </div>
</template>
<script>
import { areaQYList, companyDZList, analyse } from "@/network/inverter";
import inverterDialog from "./components/inverter_dialog.vue";
export default {
  components: {
    inverterDialog,
  },
  data() {
    return {
      showData: false,
      inverterData: [
        "总功率",
        "总容量",
        "今日有效小时数",
        "今日发电量",
        "本月发电量",
        "累计发电量",
      ],
      inverterValue: [],
      chartArry: [],
      value: "",
      options: [],
      DZvalue: "",
      DZOptions: [],
      total: 0,
      currentPage: 1,
      chartArryCurrent: [],
    };
  },
  mounted() {
    this.areaQYListFun();
  },
  methods: {
    showDeatil(item) {
      this.$refs.dialog.showDialogFun(item.pvEqmId);
    },
    /**
     * 切换企业
     * */
    changeCompany(val) {
      this.companyDZListFun(val);
    },
    /**
     * 切换电站
     * */
    changeStation(val) {},
    /**
     * 获取企业列表
     * **/
    areaQYListFun() {
      areaQYList()
        .then((res) => {
          const { content, errcode, errmsg } = res;
          this.options = content;
        })
        .catch((err) => {});
    },
    /**
     * 获取电站列表
     * **/
    companyDZListFun(val) {
      companyDZList(val)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          this.DZOptions = content;
        })
        .catch((err) => {});
    },
    /**
     * 获取电站列表
     * **/
    analyseFun() {
      analyse(this.DZvalue)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          this.inverterValue = [
            this.toFixedTwo(content.power) + "kW",
            this.toFixedTwo(content.zongrongliang) + "kWp",
            this.toFixedTwo(content.jinriyouxiaoxiaoshi) + "h",
            this.toFixedTwo(content.todayElec) + "kWh",
            this.toFixedTwo(content.elecMonth) + "kWh",
            this.toFixedTwo(content.allElec) + "kWh",
          ];
          this.chartArry = content.pvEqmLine;
          this.total = content.pvEqmLine.length;
          this.showData = true;

          this.nowInverterShow();
        })
        .catch((err) => {});
    },
    /**
     * 根据分页 当前页 展示的当前逆变器
     * **/
    nowInverterShow() {
      this.chartArryCurrent = this.chartArry.slice(
        (this.currentPage - 1) * 8,
        this.currentPage * 8
      );
    },
    /**
     * 分页切换
     * **/
    handleCurrentChange(index) {
      this.currentPage = index;
      this.nowInverterShow();
    },
    /**
     * 数据保留两位小数
     * **/
    toFixedTwo(val) {
      if (val == 0) {
        return 0;
      }
      if (val) {
        //1.先判断是不是整数，如果不是整数则保留两位小数
        let num = Number(val);
        if (!this.$isInteger(val)) {
          num = num.toFixed(2);
        }
        return num;
      }
    },
  },
};
</script>
<style scoped lang="less">
