  line-height: 0.55rem;
  letter-spacing: 0.02rem;
  position: relative;
  background: url("@/assets/powerStation/titlePng.png");
  background-size: 100% 100%;
  text-align: center;
  font-size: 0.24rem;
  font-weight: bold;
}
.box_title::after {
  display: inline-block;
  width: 0.75rem;
  height: 0.17rem;
