  display: flex;
  > div {
    margin-right: 0.48rem;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
  }
}
/deep/ .el-input__inner {
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
  color: #fff;
}
.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.47rem;
  border: none;
  cursor: pointer;
}
.center {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 0.16rem;
  > div {
    display: flex;
    align-items: center;
    width: 32.8%;
    border: 1px solid rgba(34, 175, 237, 0.5);
    background: rgba(0, 55, 146, 0.2);
    border-radius: 0.05rem;
    padding: 0.13rem 0.26rem 0.13rem 0.14rem;
    box-sizing: border-box;
