  flex-grow: 1;
  padding: 0.2rem 0rem 0.2rem 0.13rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 0;
  > div {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    > div.inverterBlock {
      width: calc(100% / 4 - 0.14rem);
      background: rgba(21, 45, 124, 0.2);
      border: 1px solid rgba(17, 96, 145, 1);
      margin: 0 0.05rem;
      > p:first-child {
        font-weight: bold;
